import { Loader } from "@pixi/loaders";
import { sounds } from "../core/SoundManager";
import { sendError } from "..";
import { BaseGame, gameSettings, METER_SIZE, stateManager, unusedBodies, world } from "../core/BaseGame";
import { finishLoading, getStorageData, startLoading } from "../core/natives/SocialManager";
import { MenuState } from "./states/MenuState";
import { GameViewState } from "./states/GameViewState";
import { Body, vec2 } from "p2";
import { CircleBlock } from "./states/game/CircleBlock";
import * as WebFont from "webfontloader";
import gsap from "gsap";
import { Ticker } from "pixi.js";
import { ComicsState } from "./states/ComicsState";

export let increaseBlocks: { block: CircleBlock; steps: number }[] = [];
export let explodePoints: [number, number][] = [];
export const calculateStars = (score: number, conditions: number[]): number => {
  let stars = 0;
  for (let i = 0; i < conditions.length; i++) {
      if (score > conditions[i]) {
          stars++;
      } else {
          break;
      }
  }
  return stars;
}
export const setSlowMo = (slowmo: boolean, physicsOnly: boolean = false)=> {
  gameSettings.slowmo = slowmo;
  if (!physicsOnly) {
    gsap.globalTimeline.timeScale(gameSettings.slowmo ? 0.3 : 1);
  }
  
}
export default class Game extends BaseGame {
  constructor() {
    super(720, 1500);
  }
  protected async createStates() {
    const skip = await getStorageData("skipcomics","0");
    stateManager.add(new MenuState());
    stateManager.add(new GameViewState());
    if (skip === "0") {
      stateManager.add(new ComicsState());
      stateManager.start(ComicsState);
    } else {
      stateManager.start(MenuState);
    }
    
  }
  protected initContactListener() {
    world.on("endContact", (evt) => {
      if (!evt.shapeA.data || !evt.shapeB.data) return;
      const bodyA: Body = evt.bodyA;
      const bodyB: Body = evt.bodyB;
      const instanceA = (bodyA as any).userData as CircleBlock;
      const instanceB = (bodyB as any).userData as CircleBlock;
      if (!instanceA.correctType || !instanceB.correctType) return;
      if ((instanceA.type === instanceB.type || instanceA.type === CircleBlock.anyType || instanceB.type === CircleBlock.anyType) && instanceA.type !== 0 && instanceB.type !== 0 && instanceA.type !== CircleBlock.goalType && instanceB.type !== CircleBlock.goalType && instanceA.type !== 250 && instanceB.type !== 250 && instanceA.type !== 251 && instanceB.type !== 251 && instanceA.type !== CircleBlock.skullType && instanceB.type !== CircleBlock.skullType) {
        instanceA.sameColorNeighbors.delete(instanceB);
        instanceB.sameColorNeighbors.delete(instanceA);
      }
    });
    world.on("beginContact", (evt) => {
      const bodyA: Body = evt.bodyA;
      const bodyB: Body = evt.bodyB;
      const instanceA = (bodyA as any).userData as CircleBlock;
      const instanceB = (bodyB as any).userData as CircleBlock;
      // heart + the end
      if (
        (instanceA.type === CircleBlock.goalType && instanceB.type === CircleBlock.endType) ||
        (instanceB.type === CircleBlock.goalType && instanceA.type === CircleBlock.endType)
      ) {
        this.levelComplete();
        return;
      }
      // heart + skull
      if (
        (instanceA.type === CircleBlock.goalType && instanceB.type === CircleBlock.skullType) ||
        (instanceB.type === CircleBlock.goalType && instanceA.type === CircleBlock.skullType)
      ) {
        if (instanceA.type === CircleBlock.skullType) {
          instanceA.boom();
        } else {
          instanceB.boom();
        }
        this.heartDeath();
        return;
      }
      // type Any + skull
      if (
        (instanceA.type === CircleBlock.anyType &&
          instanceB.type === CircleBlock.skullType) ||
        (instanceB.type === CircleBlock.anyType &&
          instanceA.type === CircleBlock.skullType)
      ) {
        instanceA.boom();
        instanceB.boom();
        return;
      }
      //rocket
      if ((instanceA as any).rocket || (instanceB as any).rocket) {
        let rocket = undefined;
        let block: CircleBlock = undefined;
        if (
          (instanceA as any).rocket &&
          instanceB.correctType() &&
          instanceB.type !== CircleBlock.goalType &&
          !instanceB.isGoal()
        ) {
          rocket = bodyA;
          block = instanceB;
        } else if (
          (instanceB as any).rocket &&
          instanceA.correctType() &&
          instanceA.type !== CircleBlock.goalType &&
          !instanceA.isGoal()
        ) {
          rocket = bodyB;
          block = instanceA;
        }
        if (rocket && block) {
          block.boom();
        }
      }
      //no shapes
      if (!evt.shapeA.data || !evt.shapeB.data) return;
      if (vec2.distance(evt.bodyA.position, evt.bodyA.previousPosition) * METER_SIZE > 0.5 || vec2.distance(evt.bodyB.position, evt.bodyB.previousPosition) * METER_SIZE > 0.5) {
        sounds.playRandom("ball hit ", 3);
      }      
      
      //main logic
      if (instanceA.correctType && instanceB.correctType) {
        if ((instanceA.type === instanceB.type || instanceA.type === CircleBlock.anyType || instanceB.type === CircleBlock.anyType) && instanceA.type !== 0 && instanceB.type !== 0 && instanceA.type !== CircleBlock.goalType && instanceB.type !== CircleBlock.goalType && instanceA.type !== 250 && instanceB.type !== 250 && instanceA.type !== 251 && instanceB.type !== 251 && instanceA.type !== CircleBlock.skullType && instanceB.type !== CircleBlock.skullType) {
          instanceA.sameColorNeighbors.set(instanceB, true);
          instanceB.sameColorNeighbors.set(instanceA, true);
        }
        let body: Body = undefined;
        let inst = undefined;
        let instB = undefined;
        if (instanceA.kinematic && instanceA.type !== 0) {
          body = bodyA;
          inst = instanceA;
          instB = instanceB;
        } else if (instanceB.kinematic && instanceB.type !== 0) {
          body = bodyB;
          inst = instanceB;
          instB = instanceA;
        }
        if (body) {
          body.gravityScale = 1;
          inst.shape.material = gameSettings.material;
          body.mass = 5;
          body.updateMassProperties();
          if (instB.type === 251) {
            instB.changePosition();
            this.shake();
          } else if (instB.type === 250) {
            this.shake();
          }
          if (instanceA.type === CircleBlock.goalType || instanceB.type === CircleBlock.goalType) return;
          if ((instanceA.type === instanceB.type || instanceA.type === CircleBlock.anyType || instanceB.type === CircleBlock.anyType) && instanceA.type !== 0 && instanceB.type !== 0 && instanceA.type !== 250 && instanceB.type !== 250 && instanceA.type !== 251 && instanceB.type !== 251 && instanceA.type !== CircleBlock.skullType && instanceB.type !== CircleBlock.skullType) {
            instanceA.kinematic = false;
            instanceB.kinematic = false;
            instanceA.body.gravityScale = 1;
            instanceB.body.gravityScale = 1;
            const steps = inst.steps;
            inst.animateAndDestroy();
            increaseBlocks.push({
              block: inst === instanceA ? instanceB : instanceA,
              steps,
            });
          } else if (instB.type === CircleBlock.skullType) {
            inst.setSkull();
          }
        } else {
          if (instanceA.kinematic) {
            bodyA.gravityScale = 1;
            instanceA.shape.material = gameSettings.material;
            bodyA.mass = 5;
            bodyA.updateMassProperties();
          } else if (instanceB.kinematic) {
            bodyB.gravityScale = 1;
            instanceB.shape.material = gameSettings.material;
            bodyB.mass = 5;
            bodyB.updateMassProperties();
          }
          if (instanceA.type === CircleBlock.goalType || instanceB.type === CircleBlock.goalType) return;
          if (
            (instanceA.type === instanceB.type || instanceA.type === CircleBlock.anyType || instanceB.type === CircleBlock.anyType) && instanceA.type !== 0 && instanceB.type !== 0 && instanceA.type !== 250 && instanceB.type !== 250 && instanceA.type !== 251 && instanceB.type !== 251 && instanceA.type !== CircleBlock.skullType && instanceB.type !== CircleBlock.skullType &&
            (instanceA.steps !== instanceB.steps ||
              instanceA.kinematic ||
              instanceB.kinematic)
          ) {
            if (instanceA.kinematic) {
              instanceA.kinematic = false;
              instanceB.kinematic = false;
              if (instanceA.body) {
                instanceA.body.gravityScale = 1;
              }
              if (instanceB.body) {
                instanceB.body.gravityScale = 1;
              }
              
              const steps = instanceA.steps;
              instanceA.animateAndDestroy();
              increaseBlocks.push({ block: instanceB, steps });
            } else {
              instanceA.kinematic = false;
              instanceB.kinematic = false;
              if (instanceA.body) {
                instanceA.body.gravityScale = 1;
              }
              if (instanceB.body) {
                instanceB.body.gravityScale = 1;
              }
              const steps = instanceB.steps;
              instanceB.animateAndDestroy();
              increaseBlocks.push({ block: instanceA, steps });
            }
            
          } else if ((instanceA.kinematic && instanceB.type === 251) ||
            (instanceB.kinematic && instanceA.type === 251)) {
              if (instanceB.type === 251) {
                this.shake();
                (instanceB as any).changePosition();
              }  else {
                this.shake();
                (instanceA as any).changePosition();
              } 
          } else if ((instanceA.kinematic && instanceB.type === CircleBlock.skullType) ||
          (instanceB.kinematic && instanceA.type === CircleBlock.skullType)) {
            (instanceB.type === CircleBlock.skullType) ? instanceA.setSkull() : instanceB.setSkull();
          }
        }
      }
    });
  }
  protected customLogic(step: number): void {
    const state = stateManager.activeState as GameViewState;
    const noState = !state || !state.unusedBlocksRemoved;
    let amount = unusedBodies.length;
    let pos = undefined;
    if (!noState) {
      state.update(step);
      if (amount > 0) {
        const body = unusedBodies[Math.floor(Math.random()*amount)];
        if (body) {
          pos = ((body as any).userData as CircleBlock).position;
        }
      }
    }
    
    unusedBodies.forEach((body) => {
      world.removeBody(body);
    });
    this.clearBodyArray();
    if (!noState) {
      increaseBlocks.forEach((data) => data.block.increase(data.steps));
      increaseBlocks = [];

      explodePoints.forEach((pos) => {
        const bodies = world.hitTest(pos, world.bodies, 0);
        if (bodies && bodies.length > 0) {
          const block = (bodies[0] as any).userData as CircleBlock;
          if (block && block.correctType && block.type !== CircleBlock.goalType && block.boom) {
          amount += block.boom();
          }
        }
      });
      amount > 0 && state.unusedBlocksRemoved(amount, pos);
    }
    explodePoints = [];
  }
  protected levelComplete() {
    (stateManager.activeState as GameViewState).levelComplete();
  }
  protected heartDeath() {
    (stateManager.activeState as GameViewState).heartDeath();
  }
  protected shake() {
    (stateManager.activeState as GameViewState).shakeScreen(Math.random() < 0.5);
  }
  protected async load() {
    return new Promise<void>((resolve, reject) => {
      sounds.init("assets/sounds");
      startLoading();
      const loader = new Loader();
      loader.add("base0", "assets/atlases/base0.json");
      loader.add("base1", "assets/atlases/base1.json");
      loader.add("levels", "assets/levels.json");
      loader.add("particles", "assets/emitter.json");
      sounds.add("music", { autoplay: true, loop: true, volume: 0.6 });
      sounds.add("ball hit 0", { volume: 0.4 });
      sounds.add("ball hit 1", { volume: 0.4 });
      sounds.add("ball hit 2", { volume: 0.4 });

      loader.onProgress.add((l) => {
        //loadingProgress(l.progress);
      });
      loader.onError.add((a, b, c) => {
        sendError({ error: `loading (${c.url})` });
        reject();
      });

      loader.load(async () => {
        await this.loadFonts();
        gameSettings.levels = loader.resources.levels.data.levels;
        gameSettings.particles = loader.resources.particles.data;
        await this.startAfterLoading();
        resolve();
      });
    });
  }
  private async loadFonts() {
    return new Promise<void>((resolve) => {
      WebFont.load({
        custom: {
            families: ["FontsFree-Net-AmericanTypewriterBold"],
            urls: ["assets/fonts/font.css"],
        },
        active: () => {
            resolve();
        },
        timeout: 60000,
    });
    });
  }
  private async startAfterLoading() {
    const preloader = document.getElementById("preloader");
    if (preloader) {
      preloader.style.display = "none";
    }
    await finishLoading();
    //await leaderboards.load();
  }
}
